/* Scss Document */
/* details CSS */

body {
	header {
		height: 100vh;
		overflow: hidden;
		position: relative;
		&:before{
		  content: "";
		  display: block;
		  padding-top: 100%;
		}
		h3 {
			text-align: right;
			font-weight: 200;
		}
		#hero_img{
			img {
			  position: absolute;
			  width: 100%;
			  height: 100%;
			  top: 0;
			  right: 0;
			  bottom: 0;
			  left: 0;
			  margin: auto;
			  object-fit: cover;
			}
		}
		#hero_txt {
			background: rgba(255,255,255,0.6);
			position: absolute;
			bottom: 0;
			width: 100%;
			h2,h3 {
				width: 100%;
				padding-right: 3rem;
			}
		}
	}
	#conts {
		.container {
			h3 {
				font-size:  2rem !important;
			}
			#work_list {
				padding-top: 5rem;
				.modal-header {
					border-bottom: none;
					padding-top: 0.5rem;
					padding-bottom: 0.5rem;
				}
				.modal-body {
					padding-top: 0;
				}
				.modal-xl {
					max-width: 1140px;
				}
			}
		}
	}
}
/*  CSS Media Query: On screens that are 1199px or less */
@media (max-width: 1199px) {
	#conts {
		header {
			height: auto !important;
			#hero_txt {
				h2, h3 {
					padding-right: 1rem !important;
				}
				h2 {
					padding-top:  0.3rem;
					font-size:  2rem !important;
					margin-bottom:  0 !important;
				}
				h3 {
					font-size:  1rem !important;
				}
			}
		}
		#servies {
			padding-bottom:  3rem;
			.conts_texts {
				padding-top:  0 !important;
				padding-bottom:  1rem !important;
			}
		}
		#work_list {	
			.card {
				width: 100% !important;
				h5 {
					font-size:  0.9rem !important;
				}
			}
			.carousel-indicators {
				bottom: 0 !important;
			}
			.carousel-caption {
				bottom: 0 !important;
				h5 {
					font-size:  0.9rem !important;
				}
			}
		}
	}
}
